import { NAVBAR_HEIGHT } from '@/lib/constants'
import Image from 'next/image'
import Link from 'next/link'
import React from 'react'

const Navbar = () => {
  return (
    <div 
      className='fixed top-0 left-0 right-0 w-full z-50 shadow-md'
      style={{ height: `${NAVBAR_HEIGHT}px` }}
    >
        <div className='flex items-center justify-between w-full px-8 py-3 bg-primary-10 text-white'>
            <div className='flex items-center gap-4 md:gap-6'>
                <Link href='/' className='cursor-pointer hover:!text-primary-80' scroll={false}>
                 <div className='flex items-center gap-3'>
                    <Image src='/logo.svg' alt='logo' width={32} height={32} className='w-6 h-6' />
                     <div className='text-xl font-bold'>
                        Khoone
                        <span className='text-primary-700 font-light hover:!text-primary-80'>Rah</span>
                     </div>
                 </div>
                </Link>
            </div>
            <p className='text-primary-200 hidden md:block'>
               <span className='text-primary-700 font-bold'>ملک</span> رویاهات رو چه اجاره، چه خرید و فروش، با بهترین ویژگی‌ها پیدا کن
            </p>
            <div className='flex items-center gap-5'>
               <Link href="/signup" className='cursor-pointer hover:!text-primary-80'>
                 <Button className='bg-primary-700 text-white px-4 py-2 rounded-md'>
                   ثبت نام 
                 </Button>
               </Link>
            </div>
        </div>
    </div>
  )
}

export default Navbar